.header {
	padding: 30px 88px;
	background-color: $secondary;
	color: #fff;
	display: flex;
	justify-content: space-between;
    align-items: center;
    @include breakpoint(phone) {
        padding: 10px 0 0 0;
        flex-direction: column;
        justify-content: center;
        img {
            width: 48px;
            padding: 0 0 10px 0;
        }
    }
}
.nav {
    &-list {
        @include breakpoint(phone) {
            flex-wrap: wrap;
		    width: 100%;
        } 
    }
    &-item {
        padding-left: 64px;
        
        @include breakpoint(pad) {
            padding-left: 40px;
        }
        @include breakpoint(phone) {
            width: 50%;
            padding: 0px;
            text-align: center;
            border: 1px solid #fff;
        }
    }
    &-link {
        display: block;
        &:hover {
			color: $hovercolor;
            position: relative; /* 當滑鼠經過超連結時，超連結往右下位移1像素 */
            top: 1px;
            left: 1px;
        }
        color: #fff;
        font-size: 2.5rem;
        @include breakpoint(phone) {
            width: 100%;
            padding: 0;
            &:hover {
                background-color: rgb(167, 123, 123);
                color: $white;
            }
        }
    }
}
.logo {
    @include breakpoint(phone) {
        padding: 10px;
    }   
}
